@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@1,300&display=swap);
*{margin:0;padding:0;font-family:"Roboto Mono",monospace;scroll-behavior:smooth}#root{background-color:#1d1516}#containerNavbar{height:10vh}#containerNavbar #navLinks{width:8vw}#introduction{height:100vh;background-color:#170402}#intro{font-family:"Source Sans Pro",sans-serif}#imgP{border:1px solid #fff}#containerAbout{width:50vw;top:150%;left:50%;margin-right:-50%;transform:translate(-50%, -50%);position:absolute;color:#000}@media only screen and (max-width: 500px){#about{height:100vh}#containerAbout{width:90vw;top:165%;left:50%;margin-right:-50%;transform:translate(-50%, -50%);position:absolute;color:#000}#icons{margin-top:2vh}}@media only screen and (max-width: 375px){#about{height:115vh}#containerAbout{width:90vw;top:170%;left:50%;margin-right:-50%;transform:translate(-50%, -50%);position:absolute;color:#000}#containerAbout p{font-size:.9rem}#icons{margin-top:2vh}}#experience{background-color:#170402}#cardProject{background-color:#300704}#buttonProject{background-color:#52110d}#tagsLinks{text-decoration:none;color:#d38888}#tagsLinks:hover{color:#884a4a}#skills{background-color:#170402}
